.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #14d8ea;
  color: #282b2b;
}

.page-name {
  margin-right: auto;
}

.button-container {
  display: flex;
}

.button {
  margin-left: 10px;
  text-decoration: none;
  color: #201e1e;
  background-color: #22b1e0; /* Background color for buttons */
  padding: 8px 16px; /* Add padding to make buttons larger */
  border-radius: 4px; /* Optional: Add rounded corners */
}

.button:hover {
  text-decoration: underline;
  background-color: #f6f9fc; /* Change background color on hover */
}
